import React from 'react';
import PropTypes from 'prop-types';
import "./UserDetails.scss";
import ContentHeader from "../ContentHeader/ContentHeader";
import BingMap from "../BingMap/BingMap";
import HideIf from "../HideIf";
import {fileImageCompression} from "../../utils";
import downloadImage from "../../images/download-white.png";

export default class UserDetails extends React.Component {
    __nameRef = React.createRef();
    __editRef = React.createRef();
    __fileRef = React.createRef();
    __isMounted = false;
    
    state = {
        image: this.props.image,
        name: this.props.name,
        aboutMe: this.props.aboutMe,
        lat: this.props.location.lat,
        long: this.props.location.long,
        loadingMap: true,
        loadingImage: false
    };
    
    componentDidMount() {
        this.__isMounted = true;
        this.props.editField === "all" ? this.__nameRef.current && this.__nameRef.current.focus() : this.__editRef.current && this.__editRef.current.focus();
    };
    
    componentWillUnmount() {
        this.__isMounted = false;
    }
    
    onSaveClick = (e) => {
        e.preventDefault();
        
        const {
            id,
            onSave,
            onClose
        } = this.props;
        
        const {
            image,
            name,
            aboutMe,
            lat,
            long
        } = this.state;
        
        if (name.trim() === '') {
            this.props.editField === "all" ? this.__nameRef.current && this.__nameRef.current.focus() : this.__editRef.current && this.__editRef.current.focus();
            return
        }
        
        if (typeof this.props.onSave === 'function') onSave({
            id,
            image,
            name,
            aboutMe,
            location: {
                lat,
                long
            }
        });
        
        if (typeof this.props.onClose === 'function') onClose();
    };
    
    changeHandler = (e) => {
        this.setState({
            [e.target.name]: (e.target.name === 'lat' || e.target.name === 'long') ? (+e.target.value || 0) : e.target.value
        })
    };
    
    onMapLoadComplete = () => {
        this.setState({loadingMap: false});
    };
    
    onMapClick = (location) => {
        if (this.props.editField !== 'location' && this.props.editField !== 'all') return;
        
        this.setState({
            lat: location.latitude,
            long: location.longitude
        })
    };
    
    fileChangeHandler = (e) => {
        e.preventDefault();
        
        this.setState({
            loadingImage: true
        });
        
        const imageFile = e.currentTarget.files[0];
        
        fileImageCompression(imageFile, (dataUrl) => { // success callback
            if (this.__isMounted) this.setState({
                image: dataUrl,
                loadingImage: false
            })
        }, (error) => { // error callback
            if (this.__isMounted) this.setState({
                loadingImage: false
            })
        })
    };
    
    onImageClick = (e) => {
        e.preventDefault();
        
        if (this.props.editField === '') return;
        this.__fileRef.current.click();
    };
    
    onLocationReset = (e) => {
        e.preventDefault();
        
        this.setState({
            lat: this.props.location.lat,
            long: this.props.location.long
        })
    };
    
    render() {
        const {
            id,
            onClose,
            editField
        } = this.props;
        
        const {
            image,
            name,
            aboutMe,
            lat,
            long,
            loadingMap,
            loadingImage
        } = this.state;
        
        return (
            <div className="user-details">
                <div className={'modal'}>
                    <ContentHeader
                        size={'medium'}
                        title={'User Details'}
                        onClose={onClose}
                        onSave={editField !== '' ? this.onSaveClick : null}
                    />
                    
                    <div className={'details'}>
                        <input
                            style={{
                                display: 'none'
                            }}
                            ref={this.__fileRef}
                            type='file'
                            accept="image/*"
                            onChange={this.fileChangeHandler}
                        />
                        <div
                            className={'image'}
                            style={{
                                backgroundImage: (image && image !== '' ? `url(${image})` : ''),
                            }}
                            onClick={this.onImageClick}
                        >
                            <HideIf condition={!loadingImage}>
                                <div className={'loading'}>
                                    <div className={'loader'}/>
                                </div>
                            </HideIf>
                        </div>
                        
                        <HideIf condition={loadingImage || !(image && image !== '')}>
                            <div className={'download'}>
                                <a
                                    download={
                                        `${name.toLowerCase().replace(/ ./g, (m) => {
                                            return m.trim().toUpperCase()
                                        })}.jpg`
                                    }
                                    href={image}
                                >
                                    <img
                                        alt=""
                                        src={downloadImage}
                                        height={'20px'}
                                    />
                                </a>
                            </div>
                        </HideIf>
                        
                        <div className={'id'}>
                            <label>ID</label>
                            {id}
                        </div>
                        
                        <div className={'name'}>
                            <label>Name</label>
                            
                            <HideIf condition={editField === "name" || editField === "all"}>
                                <span>{name}</span>
                            </HideIf>
                            
                            <HideIf condition={editField !== "name" && editField !== "all"}>
                                <input
                                    ref={editField === "all" ? this.__nameRef : this.__editRef}
                                    type="text"
                                    value={name}
                                    name={'name'}
                                    onChange={this.changeHandler}
                                />
                            </HideIf>
                        </div>
                        
                        <div className={'aboutMe'}>
                            <label>About Me</label>
                            
                            <HideIf condition={editField === "about" || editField === "all"}>
                                <span>{aboutMe}</span>
                            </HideIf>
                            
                            <HideIf condition={editField !== "about" && editField !== "all"}>
                                <textarea
                                    ref={this.__editRef}
                                    value={aboutMe}
                                    name={'aboutMe'}
                                    onChange={this.changeHandler}
                                />
                            </HideIf>
                        </div>
                        
                        <div className={'location'}>
                            <BingMap
                                overlay={false}
                                center={[
                                    lat,
                                    long
                                ]}
                                pins={[
                                    {
                                        "location": [lat, long],
                                        "infoboxOption": {title: `${lat}, ${long}`},
                                        "pushPinOption": {color: "#ce774a", title: name}
                                    }
                                ]}
                                onLoad={this.onMapLoadComplete}
                                onMapClick={this.onMapClick}
                            />
                            
                            <HideIf condition={!loadingMap}>
                                <div className={'loading'}>
                                    <div className={'loader'}/>
                                </div>
                            </HideIf>
                            
                            <HideIf condition={editField !== "location" && editField !== "all"}>
                                <div className={'edit-field'}>
                                    <label>Enter location coordinates or click on the map</label>
                                    <span>Latitude: </span>
                                    <input
                                        ref={this.__editRef}
                                        type="number"
                                        value={lat}
                                        name={'lat'}
                                        onChange={this.changeHandler}
                                    />
                                    &nbsp;&nbsp;&nbsp;
                                    <span>Longitude: </span>
                                    <input
                                        type="number"
                                        value={long}
                                        name={'long'}
                                        onChange={this.changeHandler}
                                    />
                                    <div className={'reset-location'} onClick={this.onLocationReset}/>
                                </div>
                            </HideIf>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

UserDetails.propTypes = {
    id: PropTypes.number.isRequired,
    editField: PropTypes.string,
    name: PropTypes.string.isRequired,
    aboutMe: PropTypes.string,
    location: PropTypes.shape({
        lat: PropTypes.number.isRequired,
        long: PropTypes.number.isRequired
    }),
    onSave: PropTypes.func,
    onClose: PropTypes.func
};

UserDetails.defaultProps = {
    editField: '',
    location: {
        lat: 10.1735257,
        long: 124.3534546
    }
};