import React from 'react'
import PropTypes from 'prop-types'

export default class User extends React.Component {
    render() {
        const {selected, image, id, name, aboutMe, location, onSelect, onIdClick, onNameClick, onAboutClick, onLocationClick} = this.props;
        
        return (
            <div className={'item-wrapper' + (selected ? ' selected' : '')}>
                <div className="item picker">
                    <input type="checkbox" checked={selected} onChange={onSelect}/>
                </div>
                <div className="item id" onClick={onIdClick}>{id}</div>
                <div className="item name" title={name} onClick={onNameClick}>
                    <div className={'img'} style={{
                        backgroundImage: (image && image !== '' ? `url(${image})` : ''),
                    }}/>
                    {name}
                </div>
                <div className="item about" title={aboutMe} onClick={onAboutClick}>{aboutMe}</div>
                <div className="item location" title={`${location.lat}, ${location.long}`}
                     onClick={onLocationClick}>{`${location.lat}, ${location.long}`}</div>
            </div>
        );
    }
}

User.propTypes = {
    selected: PropTypes.bool,
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
    image: PropTypes.string,
    aboutMe: PropTypes.string,
    location: PropTypes.shape({
        lat: PropTypes.number.isRequired,
        long: PropTypes.number.isRequired
    }),
    onIdClick: PropTypes.func,
    onNameClick: PropTypes.func,
    onAboutClick: PropTypes.func,
    onLocationClick: PropTypes.func
};

User.defaultProps = {
    selected: false,
    location: {
        lat: 10.1735257,
        long: 124.3534546
    },
    onIdClick: () => {},
    onNameClick: () => {},
    onAboutClick: () => {},
    onLocationClick: () => {}
};