import React from 'react'
import PropTypes from 'prop-types'
import "./ContentHeader.scss"

export default class ContentHeader extends React.Component {
    render() {
        const {
            onClose,
            onSave,
            onDelete,
            onAdd,
            buttonAddLabel,
            buttonDeleteLabel,
            buttonSaveLabel,
            buttonCloseLabel,
            size
        } = this.props;
        
        return (
            <div className={`content-header ${size}`}>
                <div className={'title'}>{this.props.title}</div>
                <div className={'buttons'}>
                    {
                        typeof onSave === "function" && <div className={'save'} onClick={onSave}>{buttonSaveLabel || 'Save'}</div>
                    }
                    {
                        typeof onDelete === "function" && <div className={'delete'} onClick={onDelete}>{ buttonDeleteLabel || 'Delete'}</div>
                    }
                    {
                        typeof onAdd === "function" && <div className={'add'} onClick={onAdd}>{buttonAddLabel || 'Add'}</div>
                    }
                    {
                        typeof onClose === "function" && <div className={'close'} onClick={onClose}>{buttonCloseLabel || 'Close'}</div>
                    }
                </div>
            </div>
        );
    }
}

ContentHeader.propTypes = {
    size: PropTypes.string,
    title: PropTypes.string.isRequired,
    buttonAddLabel: PropTypes.string,
    buttonSaveLabel: PropTypes.string,
    buttonCloseLabel: PropTypes.string,
    buttonDeleteLabel: PropTypes.string,
    onAdd: PropTypes.func,
    onSave: PropTypes.func,
    onDelete: PropTypes.func,
    onClose: PropTypes.func
};

ContentHeader.defaultProps = {
    size: 'large'   // medium, default(large)
};