export const getMaxKey = (array, key) => {
    if (!Array.isArray(array) || (array && array.length < 1) || array[0][key] === undefined) return -1;
    
    let max = -1;
    array.map(u => {
        if (u[key] > max) max = u[key];
        return null
    });
    
    return max;
};

export const hasLocalStorage = localStorage && typeof localStorage.setItem === 'function' && typeof localStorage.getItem === 'function';

export const getStoredIfAny = () => {
    if (!hasLocalStorage) return users;
    
    const kineticTestUsers = JSON.parse(localStorage.getItem('kineticTestUsers'));
    let thisUsers;
    if (kineticTestUsers && kineticTestUsers.length > 0) {
        thisUsers = kineticTestUsers;
    } else {
        thisUsers = users;
        localStorage.setItem('sortAsc', true);
    }
    
    return thisUsers;
};

export const getStoredSortSettings = () => {
    if (!hasLocalStorage) return true;
    
    const sortAsc = localStorage.getItem('sortAsc');
    
    if (sortAsc) return sortAsc === 'true';
    
    // default if none
    return true
};

export const observer = (element, callback, config, ref) => {
    if (typeof MutationObserver !== 'function') return;
    
    if (!element) {
        console.log('Invalid element: ', element);
        return
    }
    
    if (typeof callback !== 'function') {
        console.log('No callback specified!');
        return
    }
    
    let defaultConfig = {
        attributes: true,
        childList: false,
        subtree: false
    };
    
    if (typeof config === 'object') {
        defaultConfig = {
            ...defaultConfig,
            ...config
        };
    }
    
    const observer = new MutationObserver((mutationsList, observer) => {
        callback(mutationsList, observer);
    });
    
    observer.observe(element, defaultConfig);
    
    if (typeof ref === 'function') ref(observer);
};

export const toFixed = (number, digit) => {
    number = +number;
    digit = (+digit || 2);
    
    if (Number.isNaN(number) || number === 0) return number;
    
    return number.toFixed(digit).replace(/\.00$/, '');
};