import React, {Fragment} from 'react';
import "./UsersList.scss";
import ContentHeader from "../ContentHeader/ContentHeader";
import {
    getStoredIfAny,
    getStoredSortSettings,
    hasLocalStorage,
    getMaxKey
} from "../../utils";
import User from "./User";
import HideIf from "../HideIf";
import UserDetails from "../UserDetails/UserDetails";

export default class UsersList extends React.Component {
    state = {
        users: getStoredIfAny(),
        totalRows: getStoredIfAny().length,
        pageRows: 5,
        currentPage: 1,
        selectAll: false,
        userDetails: {
            show: false,
            id: -1,
            image: '',
            name: '',
            aboutMe: '',
            location: {
                lat: 0,
                long: 0
            }
        },
        sortAsc: getStoredSortSettings(),
        editField: '',
        hasSelection: false
    };
    
    onNavButtonClick = (e) => {
        if (e.target.classList.contains('disabled')) {
            return
        }
        
        const btn = e.target.getAttribute('name');
        
        let thisPage;
        switch (btn) {
            case 'first':
                thisPage = 1;
                break;
            case 'last':
                thisPage = Math.ceil(this.state.totalRows / this.state.pageRows);
                break;
            case 'previous':
                thisPage = this.state.currentPage - 1;
                break;
            default:
                thisPage = this.state.currentPage + 1;
        }
        
        this.setState({
            currentPage: thisPage,
            selectAll: false
        })
    };
    
    getPageLabel = () => {
        const {totalRows} = this.state;
        
        return totalRows ? `${this.getItemRange().join('-')} of ${totalRows}` : []
    };
    
    getItemRange = () => {
        const {totalRows, pageRows, currentPage} = this.state;
        const lastPageItem = pageRows * currentPage;
        return totalRows ? [lastPageItem - (pageRows - 1), lastPageItem > totalRows ? totalRows : lastPageItem] : []
    };
    
    textChangeHandler = (e) => {
        let thisState = {[e.target.name]: e.target.value};
        
        // reset currentPage whenever pageRows is updated
        if (e.target.name === 'pageRows') {
            thisState.currentPage = 1;
            thisState.pageRows = +thisState.pageRows;
        }
        
        this.setState(thisState);
    };
    
    onUserSelectAll = (e) => {
        if (this.state.totalRows < 1) return;
        
        const pageRange = this.getItemRange();
        const users = this.state.users.map((u, i) => {
            if (i >= (pageRange[0] - 1) && i <= (pageRange[1] - 1)) u.selected = e.target.checked;
            return u
        });
        
        this.setState({
            users,
            selectAll: !this.state.selectAll,
            hasSelection: this.getSelected(users).length > 0
        });
        
        this.updateLocalStorage(users)
    };
    
    onUserSelect = (id) => {
        const users = this.state.users.map(u => {
            if (u.id === id) u.selected = !u.selected;
            return u
        });
        
        this.setState({
            users,
            hasSelection: this.getSelected(users).length > 0
        });
        
        this.updateLocalStorage(users)
    };
    
    getSelected = (list) => {
        return list.filter(u => u.selected);
    };
    
    onUserClick = (user, edit) => {
        this.setState({
            userDetails: {
                ...user,
                show: true
            },
            editField: edit
        })
    };
    
    detailCloseHandler = () => {
        this.setState({
            editField: '',
            userDetails: {
                ...this.state.userDetails,
                show: false
            }
        })
    };
    
    detailSaveHandler = (user) => {
        let existingUsers = this.state.users;
        
        let thisState = this.state.editField === 'all' ? {
            users: existingUsers.concat({
                selected: false,
                id: getMaxKey(existingUsers, 'id') + 1,
                image: user.image,
                name: user.name,
                aboutMe: user.aboutMe,
                location: {
                    lat: +user.location.lat || 0,
                    long: +user.location.long || 0
                }
            }),
            totalRows: existingUsers.length + 1
        } : {
            users: existingUsers.map(u => {
                if (u.id === user.id) {
                    u.image = user.image;
                    u.name = user.name;
                    u.aboutMe = user.aboutMe;
                    u.location = {
                        lat: +user.location.lat || 0,
                        long: +user.location.long || 0
                    };
                }
                return u
            })
        };
        
        this.setState(thisState);
        
        this.updateLocalStorage(thisState.users);
    };
    
    ascSort = (a, b) => {
        let c = 0;
        if (a.id > b.id) c = 1; else if (a.id < b.id) c = -1;
        
        return c
    };
    
    dscSort = (a, b) => {
        let c = 0;
        if (a.id > b.id) c = -1; else if (a.id < b.id) c = 1;
        
        return c
    };
    
    onSortToggleClick = () => {
        const sortAsc = !this.state.sortAsc;
        const users = this.state.users.sort(sortAsc ? this.ascSort : this.dscSort);
        
        this.setState({
            sortAsc,
            users,
            currentPage: 1 // reset currentPage whenever pageRows is updated
        });
        
        this.updateLocalStorage(users, sortAsc);
    };
    
    addNewUser = (e) => {
        e.preventDefault();
        
        this.setState({
            editField: 'all',
            userDetails: {
                show: true,
                id: getMaxKey(this.state.users, 'id') + 1,
                image: '',
                name: '',
                aboutMe: '',
                location: {
                    lat: 0,
                    long: 0
                }
            }
        })
    };
    
    removeSelected = (e) => {
        e.preventDefault();
        
        const users = this.state.users.filter(u => !u.selected);
        
        this.setState({
            users: users,
            // reset
            hasSelection: false,
            selectAll: false,
            totalRows: users.length,
            currentPage: 1
        });
        
        this.updateLocalStorage(users);
    };
    
    componentDidMount() {
        if (hasLocalStorage && !localStorage.getItem('kineticTestUsers')) {
            this.updateLocalStorage()
        }
        
        this.setState({
            hasSelection: this.getSelected(this.state.users).length > 0
        });
    }
    
    updateLocalStorage = (users, sortAsc) => {
        if (hasLocalStorage) {
            try {
                localStorage.setItem('kineticTestUsers', JSON.stringify(users || this.state.users));
                localStorage.setItem('sortAsc', typeof sortAsc === 'boolean' ? sortAsc : this.state.sortAsc);
            } catch (error) {
                console.error(error);
            }
        }
    };
    
    render() {
        const {
            users,
            pageRows,
            currentPage,
            totalRows,
            selectAll,
            userDetails,
            sortAsc,
            editField,
            hasSelection
        } = this.state;
        const itemRange = this.getItemRange();
        const thisUsers = users.filter((u, i) => i >= (itemRange[0] - 1) && i <= (itemRange[1] - 1));
        
        return (
            <Fragment>
                <div className={'users-list'}>
                    <ContentHeader
                        title={'Users'}
                        buttonDeleteLabel={'Remove Selected'}
                        buttonSaveLabel={'Add User'}
                        onAdd={this.addNewUser}
                        onDelete={hasSelection ? this.removeSelected : null}
                    />
                    
                    <div className={'list'}>
                        <div className={'list-header'}>
                            <div className="header picker">
                                <input type="checkbox" checked={selectAll} onChange={this.onUserSelectAll}/>
                            </div>
                            
                            <div
                                className="header id"
                                onClick={this.onSortToggleClick}
                            >
                                <span>{sortAsc ? '↑' : '↓'}</span>
                                ID
                            </div>
                            
                            <div className="header name">Name</div>
                            
                            <div className="header about">About Me</div>
                            
                            <div className="header location">Location</div>
                        </div>
                        <div className={'list-content'}>
                            {
                                thisUsers.map(u => {
                                    return <User
                                        {...u}
                                        onSelectAll={this.onUserSelectAll}
                                        onSelect={() => this.onUserSelect(u.id)}
                                        onIdClick={() => this.onUserClick(u, '')}
                                        onNameClick={() => this.onUserClick(u, 'name')}
                                        onAboutClick={() => this.onUserClick(u, 'about')}
                                        onLocationClick={() => this.onUserClick(u, 'location')}
                                        key={u.id}
                                    />
                                })
                            }
                            
                            <div className={'pagination'}>
                                <div className={'label'}>Rows per page:</div>
                                
                                <div className={'pageRows'}>
                                    <select name={'pageRows'} value={pageRows} onChange={this.textChangeHandler}>
                                        <option value={3}>3</option>
                                        <option value={5}>5</option>
                                        <option value={10}>10</option>
                                        <option value={20}>20</option>
                                    </select>
                                </div>
                                
                                <div className={'totalRows'}>{this.getPageLabel()}</div>
                                
                                <div className={'buttons'}>
                                    <div
                                        name={'first'}
                                        className={currentPage === 1 ? 'disabled' : ''}
                                        onClick={this.onNavButtonClick}
                                    />
                                    
                                    <div
                                        name={'previous'}
                                        className={currentPage === 1 ? 'disabled' : ''}
                                        onClick={this.onNavButtonClick}
                                    />
                                    
                                    <div
                                        name={'next'}
                                        className={(currentPage * pageRows) >= totalRows ? 'disabled' : ''}
                                        onClick={this.onNavButtonClick}
                                    />
                                    
                                    <div
                                        name={'last'}
                                        className={(currentPage * pageRows) >= totalRows ? 'disabled' : ''}
                                        onClick={this.onNavButtonClick}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <HideIf condition={!userDetails.show}>
                    <UserDetails
                        {...userDetails}
                        editField={editField}
                        onSave={this.detailSaveHandler}
                        onClose={this.detailCloseHandler}
                    />
                </HideIf>
            </Fragment>
        );
    }
}